<!--  -->
<template>
  <div class="page">
    <div class="title">提现</div>
    <div class="body">
      <div class="applyFor">
        <div class="form">
          <div class="forTile">可提现金额0元</div>
          <h3>申请提现</h3>
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="银行">
              <el-input placeholder="请输入" v-model="form.name" label-width="80px"></el-input>
            </el-form-item>
            <el-form-item label="开户支行">
              <el-input placeholder="请输入开户支行" v-model="form.name" label-width="80px"></el-input>
            </el-form-item>
            <el-form-item label="开户省市">
              <el-input class="region" v-model="form.name" label-width="80px" placeholder="请输入省"></el-input>-
              <el-input class="region" v-model="form.name" label-width="80px" placeholder="请输入市"></el-input>
            </el-form-item>
            <el-form-item label="银行卡">
              <el-input placeholder="请输入卡号" v-model="form.name" label-width="80px"></el-input>
            </el-form-item>
            <el-form-item label="姓名">
              <el-input placeholder="请输入" v-model="form.name" label-width="80px"></el-input>
            </el-form-item>
            <el-form-item label="申请金额">
              <el-input v-model="form.name" label-width="80px"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">立即创建</el-button>
            </el-form-item>
            <div class="text">备注：提取金额为上一个月的金额</div>
          </el-form>
        </div>
      </div>
      <div class="record">
        <h3 class="recordTitle">提现申请</h3>
        <div class="table">
          <el-table :header-cell-style="{background:'#000',color:'#fff'}" :data="tableData" style="width: 100%">
            <el-table-column prop="date" label="日期" width="180"></el-table-column>
            <el-table-column prop="name" label="姓名" width="180"></el-table-column>
            <el-table-column prop="address" label="地址"></el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    return {
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄"
        }
      ]
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  }
};
</script>
<style lang='stylus' scoped>
.page {
  border: 1px solid #ccc;
  height: 800px;

  .body {
      position:relative
      .record{
          position:absolute
          right:20px
          top:2%
          width:50%
        .recordTitle{
            text-align:center
        }  
        .table{
            margin-top:20px
        }
      }
  }   
  .title {
    padding-left: 10px;
    height: 40px;
    line-height: 40px;
    background: #eee;
    border-bottom: 1px solid #ccc;
  }

  .form {
    width: 45%;
    padding-left: 10px;
    border-right: 1px solid #ccc;
    margin-top: 20px;

    .forTile {
      width: 95%;
      padding-left: 10px;
      border-bottom: 1px solid #ccc;
      height: 40px;
      line-height: 40px;
    }

    h3 {
      text-align: center;
      height: 50px;
      line-height: 50px;
    }

    .el-input {
      width: 350px;
    }

    .region {
      width: 168px;
    }

    .text {
      text-align: center;
      color: #ccc;
    }
  }
}
</style>


