<template>
  <div class="page">
      <el-col :span="24">
        <div class="title">礼品淘-分站后台管理系统</div>
        <div class="user">
          <div>
              <a href="#">
                <i class="iconfont icon-LC_icon_user_fill_3"></i>
              </a>
          </div>
          <div>
            <a href="#">admin</a>
          </div>
          <div>|</div>
          <div>
            <a href="#">修改密码</a>
          </div>
          <div>
            <a href="#">退出登录</a>
          </div>
        </div>
      </el-col>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {};
  },
  components: {}
};
</script>

<style scoped lang='stylus'>
.page {
  width: 100%;
  background: #ec3440;
  height: 60px;
  
}
.el-col{
 position: relative;
}
.el-col>div {
  float: left;
  &.title {
    position: absolute
    left: 20px;
    color: #fff;
    line-height: 60px;
  }
}

.el-col .user {
  position: absolute
  right: 20px;
  &>div {
    float: left;
    color: #fff;
    line-height: 60px;
    margin-left:10px;
  }
}

a{ 
    text-decoration:none; 
    color:#fff; 
} 
</style>



