<!--  -->
<template>
  <div>
    <el-row>
      <el-col :span="24">
        <div class="title">&nbsp;&nbsp;&nbsp;&nbsp;用户管理>任务列表</div>
        <div class="wrap">
          <div class="inputWrap">
            <el-col :span="10">
              <span>支付时间</span>&nbsp;&nbsp;&nbsp;
              <el-date-picker
                v-model="value1"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
              ></el-date-picker>
            </el-col>
            <el-col :span="7">
              <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="审批人">
                  <el-input v-model="formInline.user" placeholder="审批人" align="center"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="7">
              <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="审批人">
                  <el-input v-model="formInline.user" placeholder="审批人" align="center"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
          </div>
          <div class="btn">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="支付订单号">
                  <el-input v-model="formInline.user" placeholder="审批人" align="center"></el-input>
                </el-form-item>
                <el-button>搜索</el-button>
                <el-button>导出报表</el-button>
            </el-form>
              
          </div>
        </div>
      </el-col>
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{background:'#000',color:'#fff'}"
    >
      <el-table-column prop="name" label="平台任务号" align="center"></el-table-column>
      <el-table-column prop="name" label="平台" align="center"></el-table-column>
      <el-table-column prop="name" label="方式" align="center"></el-table-column>
      <el-table-column prop="name" label="用户账户号" align="center"></el-table-column>
      <el-table-column prop="name" label="任务金额" align="center"></el-table-column>
      <el-table-column prop="name" label="包裹数量" align="center"></el-table-column>
      <el-table-column prop="name" label="支付金额" align="center"></el-table-column>
      
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value1: "",
      value2: "",
      formInline: {
        user: "",
        region: ""
      },
      tableData: [{
            date: '2016-05-02',
            name: '王小虎',
          },
          {
            date: '2016-05-02',
            name: '王小虎',
          },{
            date: '2016-05-02',
            name: '王小虎',
          },{
            date: '2016-05-02',
            name: '王小虎',
          }]
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  }
};
</script>
<style lang='stylus' scoped>
.el-col-24
  margin-bottom 20px
  .title
    height 40px
    line-height 40px
    border solid 1px #ccc
  .wrap
    border 1px solid #ccc  
    background #fff
    .inputWrap
      overflow hidden
      padding-top 15px
  .el-col-10
    padding-left 35px
  .btn>.el-form
    padding-left 35px
.el-date-editor
  width 75% 
</style>


