<template>
  <el-container>
    <el-header v-show="$route.meta.showSlideBar">
      <Header></Header>
    </el-header>
    <el-container>
      <el-aside width="200px" v-show="$route.meta.showSlideBar">
        <SlideBar ></SlideBar>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import Header from './Public/Header/Header'
import SlideBar from './Public/SlideBar/SlideBar'

export default {
  name: 'App',
  components:{
    Header,
    SlideBar
  }
}
</script>

<style>
*{
  padding: 0;
  margin:0;
}
body {
  height:100%;
}
.el-container{
  margin:0;
}
.el-header {
  width: 100%;
  padding: 0;
  margin:0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.el-main{
  padding:10px;
  background:#f5f5f5;
  overflow-x: hidden;
}  
.el-aside{
  height:845px;
  background: rgb(81, 78, 78)
}
</style>



